import React, { useEffect } from "react";
import StaffMemberConfig from "../Mainbody/StaffMemberConfig";
import { useLocation } from "react-router-dom";
import ViewProfile from "../Mainbody/ViewProfile/ViewProfile";
import Complaints from "../Mainbody/Complaints/Complaints";
import Transfers from "../Mainbody/Transfers/Transfers";
import SwitchAccount from "../Mainbody/SwitchAccount/SwitchAccount";
import StaffLeaveConfig from "../Mainbody/Leaves/StaffLeaveConfig";
import StudentMessageHistory from "../../../optimized/UserStaffMember/FinanceManager/FinanceAllStudent/StudentMessageHistory/StudentMessageHistory";
import { useStaffDesignationDataQuery } from "../../../services/joining/joinApi";

const StaffSelfAccessConfig = () => {
  const location = useLocation();
  const { data: staffDesignationData, refetch: staffDesignationDataRefetch } =
    useStaffDesignationDataQuery(location?.state?.sid, {
      skip: !location?.state?.sid,
    });

  useEffect(() => {
    if (location?.state?.sid) staffDesignationDataRefetch();
  }, [location?.state?.sid, staffDesignationDataRefetch]);

  // console.info("staffDesignationData", staffDesignationData);
  return (
    <StaffMemberConfig
      index={
        location?.search?.substring(3) === "profile"
          ? "20"
          : location?.search?.substring(3) === "complaint"
          ? "21"
          : location?.search?.substring(3) === "leave"
          ? "22"
          : location?.search?.substring(3) === "transfer"
          ? "23"
          : location?.search?.substring(3) === "student-message"
          ? "60"
          : "24"
      }
      sid={location?.state?.sid ?? ""}
    >
      {location?.search?.substring(3) === "profile" && (
        <ViewProfile staffData={location?.state?.sid ?? ""} />
      )}
      {location?.search?.substring(3) === "complaint" && (
        <Complaints staffId={location?.state?.sid ?? ""} />
      )}
      {location?.search?.substring(3, 8) === "leave" && (
        <StaffLeaveConfig
          staffId={location?.state?.sid ?? ""}
          carryParentState={location.state}
        />
      )}
      {location?.search?.substring(3) === "transfer" && (
        <Transfers staffId={location?.state?.sid ?? ""} />
      )}
      {location?.search?.substring(3) === "switch" && <SwitchAccount />}
      {location?.search?.substring(3) === "student-message" && (
        <StudentMessageHistory
          instituteId={staffDesignationData?.staff?.institute?._id}
          financeId={staffDesignationData?.staff?.institute?.financeDepart?.[0]}
          headId={location?.state?.sid}
          accessAs={
            staffDesignationData?.staff?.current_designation
              ? staffDesignationData?.staff?.current_designation
              : // : staffDesignationData?.staff?.recentDesignation
                ""
          }
        />
      )}
    </StaffMemberConfig>
  );
};

export default StaffSelfAccessConfig;
